.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    padding-top: 20px;
}

.nav-logo{
    margin-left: 37px;
}

.nav-logo img{
    height: 100%;
}

.nav-line{
    width: 456px;
    height: 1px;
    background: #FFFFFF;
    mix-blend-mode: normal;
    opacity: 0.25;
    z-index: 2;
}

.nav-menu{
    width: 750px;
    display: flex;
    color: #fff;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.04);
    backdrop-filter: blur(81.5485px);
    font-family: 'Barlow Condensed';
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
}

.nav a{
    font-family: 'Barlow Condensed';
    display: flex;
    align-items: center;
    margin-right: 30px;
    text-decoration: none;
    color: #fff;
    padding: 10px 0;
    margin: 0 10px;
}

.nav a.active{
    border-bottom: 3px solid #fff;
}

.nav a h1,
.nav a h3{
    font-size: 10px;
    padding: 10px 0;
}

.nav a h3{
    margin-right: 8px;
}

/* Mobile Screen Navbar(Hamburger) */
.mobile-navbar {
    margin-left: 1rem;
    display: none;
    position: relative;
    color: #fff;
}

.mobile-navbar svg{
    cursor: pointer;
}

.nav-menu-mobile {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0;
    right: 0;
}

.mobile-navbar{
    display: none;
}


@media screen and (max-width: 770px) {
    .nav {
        max-width: 700px;
        padding: 0;
        margin: 0;
    }

    .nav-logo{
        width: 40px;
    }

    .nav-logo img{
        max-width: 30px;
        height: auto;
    }

    .nav-line{
        display: none;
        width: 0;
        height: 0;
        background: none;
    }
    
    .nav-menu{
        justify-content: flex-end;
        max-width: 450px;
        font-size: 12px;
        line-height: 14px;
    }
  
    .nav-menu-nums{
        display: none;
    }

    .nav a{
        padding: 0;
        margin: 0;
    }
}

@media (max-width: 470px) { 
    .nav{
        padding-top: 0;
    }

    .nav a{
        padding: 0;
        margin: 0;
    }

    
    .nav-menu{
        display: none;
    }

    .mobile-navbar{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px;
    }

    .nav-menu-mobile{
        max-width: 254px;
        height: 667px;
        padding: 1.2rem 0.9rem 0 1.2rem;
        background: rgba(255, 255, 255, 0.04);
        backdrop-filter: blur(81.5485px);
        z-index: 100;
    }

    .nav-menu-mobile a.active{
        border-bottom: none;
        border-right: 3px solid #fff;
    }

    .nav-menu-nums{
        display: flex;
    }

    .nav-logo img{
        max-width: 40px;
        height: 40px;
    }

   
}