.technology{
    background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url("../../assets/technology/background-technology-desktop.jpg");
    opacity: 0.95;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    max-width: 1440px;
    height: 900px;
    color: #fff;
}

.technology-head{
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 4.725px;
    text-transform: uppercase;
    color: #FFFFFF;    
    padding-left: 167px;
    padding-top: 68px;
}


.technology-num{
    opacity: 0.25;
    margin-right: 28px;
}

.technology-container{
    display: flex;
    justify-content: space-evenly;
    padding-bottom: 100px;
}

.technology-content{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 140px;
    column-gap: 80px;
}

.technology-content-text{
    display: flex;
    flex-direction: column;
}

.technology-content-num{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.technology-content-num .num{
    width: 80px;
    border: 2px solid white;
    margin-bottom: 30px;
    padding: 20px;
    border-radius: 100%;
    font-family: 'Bellefair';
    font-weight: 400;
    font-size: 28px;
    line-height: 37px;
    text-align: center;
}

.technology-content h4{
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
    text-transform: uppercase;
    
    color: #D0D6F9;
}

.technology-content h1{
    font-family: 'Bellefair';
    font-style: normal;
    font-weight: 400;
    font-size: 50px;
    line-height: 64px;
    text-transform: uppercase;
    margin-top: 12px;
    color: #FFFFFF;
}

.technology-content p{
    width: 444px;
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: #D0D6F9;
    margin-top: 18px;
}

/* Dots */
.active{
    display: flex;
}

.inactive{
    display: none;
}

.all-dot{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.dots {
    cursor: pointer;
}

.active-dot, .dot:hover{
    background: #fff;
    color: #000;
    font-weight: bold;
}

.technology-img{
    align-self: flex-end;
}

.landscape-img{
    display: none;
}

@media screen and (max-width: 768px) {
    .technology{
        background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url("../../assets/technology/background-technology-tablet.jpg");
        opacity: 0.95;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        max-width: 768px;
        height: 1024px;
    }
    
    .technology-head{
        font-size: 20px;
        line-height: 24px;
        letter-spacing: 3.375px;
        padding: 0 ;
        margin: 40px 0 0 38px;
    }

    .technology-container{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        padding: 0px;
    }

    .technology-img{
        order: 1;
    }

    .technology-content{
        order: 2;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        row-gap: 20px;
        padding: 0;
        margin: 0;
        text-align: center;
    }

    .landscape-img{
        display: block;
        margin-top: 60px;
    }

    .portrait-img{
        display: none;
    }
    
    
    .technology-content-num .num{
        width: 40px;
        border-radius: 50%;
        margin: 0;
        padding: 0;
        font-size: 20px;
        line-height: 28px;
        text-align: center;
        letter-spacing: 1.5px;
        margin-top: 56px;
    }

    
    .technology-content h4{
        margin-top: 44px;
    }
    
    .technology-content h1{
        margin-top: 16px;
        font-size: 40px;
        line-height: 46px;
        text-align: center;
    }
    
    .technology-content p{
        margin-top: 16px;
        font-size: 16px;
        line-height: 28px;
        text-align: center;
    }
    
    /* Dots */
    
    .all-dot{
        width: 210px;
        height: 60px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        column-gap: 30px;
    }    
}

@media screen and (max-width: 380px) {
    .technology{
        background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url("../../assets/technology/background-technology-mobile.jpg");
        opacity: 0.95;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        max-width: 375px;
        height: 720px;
    }
    
    .technology-head{
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 2.7px;
        padding: 0;
    }
    
    .landscape-img{
        width: 375px;
    }

    .num {
        font-size: 16px;
        line-height: 18px;
        text-align: center;
        letter-spacing: 1px;
    }

    .technology-content{
        width: 375px;
    }
    
    .technology-content h4{
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        letter-spacing: 2.3625px;
        margin-top: 26px;
    }
    
    .technology-content h1{
        font-size: 24px;
        line-height: 28px;
        text-align: center;
        margin-top: 9px;
    }
    
    .technology-content p{
        width: 327px;
        margin-top: 16px;
        font-size: 15px;
        line-height: 25px;
        text-align: center;
    }

    .all-dot{
        width: 152px;
        height: 40px;
        text-align: center;
        column-gap: 20px;
    } 

    
}