.crew{
    background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url("../../assets/crew/background-crew-desktop.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0.95;
    font-family: 'Barlow Condensed';
    font-weight: 400;
    color: #fff;
    max-width: 1440px;
}

.crew-container{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin: 60px 100px 0 100px;
}

.crew-head{
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 4.725px;
    text-transform: uppercase;
    margin-left: 167px;
}

.crew-num{ 
    mix-blend-mode: normal;
    opacity: 0.25;
    margin-right: 24px;
}

.crew-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 500px;
    font-family: 'Bellefair';
    padding-bottom: 30px;
}

.crew-content h2{
    font-size: 32px;
    line-height: 37px;
    mix-blend-mode: normal;
    opacity: 0.5;
    text-transform: uppercase;
}

.crew-content h1{
    text-transform: uppercase;
    font-size: 38px;
    line-height: 50px;
    margin-top: 15px;
}

.crew-content p{
    max-width: 444px;
    font-family: 'Barlow';
    font-size: 18px;
    line-height: 32px;
    color: #D0D6F9;
    margin-top: 27px;
}

.crew-container img{
    width: 468px;
    height: 412px;
    margin-top: 20px;
}

.act{
    display: flex;
}

.inact{
    display: none;
}

.all-dots{
    display: flex;
    justify-content: flex-start;
    width: 40%;
    column-gap: 15px;
    margin-top: 40px;
}

.dot {
    cursor: pointer;
    height: 1rem;
    width: 1rem;
    margin: 0 3px;
    background-color: rgba(0,0,0,.3);
    border-radius: 50%;
    display: inline-block;
}

.active-dot, .dot:hover{
    background-color: rgba(255, 255, 255, 0.5);
}

@media screen and (max-width: 770px) {
    .crew{
        background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url("../../assets/crew/background-crew-tablet.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        max-height: 1024px;
        max-width: 768px;
    }

    /* .crew .nav{
        height: 60px;
        padding: 0;
        margin: 0;
    } */
    
    .crew-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 0;
    }
    
    
    .crew-head{
        font-size: 20px;
        line-height: 24px;
        letter-spacing: 3.375px;
        align-self: flex-start;
        margin-left: 39px;
        margin-top: 30px;
    }
    
    .crew-content{
        max-width: 458px;
        margin-top: 40px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    
    .crew-content h2{
        font-size: 24px;
        line-height: 28px;
        text-align: center;
    }
    
    .crew-content .name{
        margin-top: 8px;
        font-size: 38px;
        line-height: 46px;
        text-align: center;
    }
    
    .crew-content p{
        max-width: 458px;
        margin-top: 10px;
        padding: 0;

        font-size: 16px;
        line-height: 28px;
        text-align: center;
    }

    .crew-img{
        margin-top: 50px;
    }
    
 
    .crew-content img{
        width: 540px;
        height: 562px;
    }
    
    .all-dots{
        height: 0%;
        width: 10%;
        margin-top: 40px;
        align-self: center;
        justify-content: center;
    }

    .dot{
        margin: 0.1rem;
        padding: 0.5rem;
        height: 0;
    }
 
}

@media  screen and (max-width: 380px) {
    .crew{
        background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url("../../assets/crew/background-crew-mobile.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 710px;
        max-width: 380px;
    }
    
    .crew-container{
        margin: 0;
        padding: 0;
    }
    
    .crew-content{
        order: 2;
        max-width: 327px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0;
    }
    
    .crew-head{
        margin-top: 34px;
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 2.7px;
    }
    
    
    .crew-content h2{
        margin-top: 32px;
        font-size: 16px;
        line-height: 18px;
        order: 2;
    }
    
    .crew-content .name{
        margin-top: 8px;
        font-size: 24px;
        line-height: 28px;
        order: 3;
    }
    
    .crew-content p{
        max-width: 327px;
        padding: 0;
        margin: 0;
        text-align: center;
        font-size: 15px;
        line-height: 25px;
        margin-top: 16px;
        order: 4;
    }
    
    .crew-img{
        order: 1;
    }
    
    .crew-img img{
        width: 177px;
        height: 223px;
        margin-top: 32px;
        margin: 0;
       
    }

    
    .all-dots{
        column-gap: 10px;
        order: 1;
    }

    .dot{
        width: 8px;
        height: 8px;
    }
}
