.destination{
    background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url("../../assets/destination/background-destination-desktop.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0.95;
    color: #fff;
    max-width: 1440px;
}

.destination-head{
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 4.725px;
    text-transform: uppercase;
    color: #FFFFFF;
    
    padding-left: 167px;
    padding-top: 68px;
}

.destination-num{
    opacity: 0.25;
    margin-right: 28px;
}

.destination-container{
    display: flex;
    padding-top: 65px;
    justify-content: space-evenly;
    align-items: center;
}

.destination-container .destination-img img{
    width: 100%;
    height: 445px;
    align-self: center;
}

.destination-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 445px;
}

.destination-content .d-nav{
    display: flex;
    column-gap: 20px;
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
    color: #FFFFFF;
}

.d-nav a{
    padding: 5px;
    cursor: pointer;
    text-decoration: none;
    color: #fff;
}

.destination-content .d-nav a.active,
.destination-content .d-nav a:hover{
    border-bottom: 3px solid #fff;
    display: flex;
}

.destination-content h2{
    font-family: 'Bellefair';
    font-style: normal;
    font-weight: 400;
    font-size: 100px;
    line-height: 115px;
    margin-top: 37px;
}

.destination-content p{
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: #D0D6F9;
}

.destination-content hr{
    margin-top: 54px;
    border: 1px solid #383B4B;
}

.destination-stats{
    display: flex;
    column-gap: 80px;
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 2.3625px;
    text-transform: uppercase;
    color: #D0D6F9;
    padding-top: 28px;
    padding-bottom: 110px;
}

@media screen and (max-width: 770px) {
    .destination{
        background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url("../../assets/destination/background-destination-tablet.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        max-width: 768px;
        height: 1024px;
    }
       
    .destination-head{
        font-size: 20px;
        line-height: 24px;
        letter-spacing: 3.375px;
        padding: 0;
        margin: 40px 0 0 40px;
    }
    
    .destination-num{
        opacity: 0.25;
        margin-right: 18px;
    }
    
    .destination-container{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 60px;
        justify-content: space-evenly;
    }

    .destination-img {
        margin-bottom: 53px;
    }
    
    .destination-container .destination-img img{
        width: 300px;
        height: 300px;
    }
    
    .destination-content{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        max-width: 445px;
    }
    
    .d-nav{
        column-gap: 36px;
    }
    
    .d-nav a{
        color: #D0D6F9;
        padding: 0;
    }
    
    
    .destination-content h2{
        font-size: 80px;
        line-height: 92px;
        text-align: center;
        color: #FFFFFF;
        margin-top: 32px;
    }
    
    .destination-content p{
        font-size: 16px;
        line-height: 28px;
        text-align: center;
    }
    
    .destination-content hr{
        width: 444px;
    }
    
    .destination-stats{
        letter-spacing: 2px;
    }
}

@media (max-width: 470px) {
    .destination-content hr{
        width: 375px;
    }
    
}


@media screen and (max-width: 380px) {
    .destination{
        background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url("../../assets/destination/background-destination-mobile.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        max-width: 375px;
        height: 850px;
    }
    
    .destination-head{
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 2.7px;
        text-align: center;
        margin: 24px 0 0;
        
    }

    .destination-num{
        opacity: 0.25;
        margin-right: 18px;
    }
    
    .destination-container .destination-img img{
        width: 170px;
        height: 170px;
    }
    
    .destination-content{
        width: 330px;
    }
    
    .d-nav{
        column-gap: 27px;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 2.3625px;
    }
    
    .d-nav a{
        color: #D0D6F9;
        padding: 10px 0;
    }
    
    
    .destination-content h2{
        font-size: 56px;
        line-height: 64px;
        margin-top: 20px;
    }
    
    .destination-content p{
        width: 327px;
        font-size: 15px;
        line-height: 25px;
    }
    
    .destination-content hr{
        max-width: 327px;
        margin-top: 32px;
    }
    
    .destination-stats{
        flex-direction: column;
        justify-content: space-around;
        row-gap: 32px;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 2.3625px;
        padding-bottom: 0;
    }
}
