.home{
    background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url('../../assets/home/background-home-desktop.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    max-width: 1440px;
  }
  

.home-container{
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
    padding-top: 255px;
    padding-bottom: 40px;
}

.home-text{
    max-width: 450px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.home-text h4{
    font-family: 'Barlow Condensed';
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 4px;
    color: #D0D6F9;
}

.home-text h1{
    font-family: 'Bellefair';
    font-weight: 400;
    font-size: 130px;
    line-height: 172px;
    color: #FFFFFF;
}

.home-text p{
    font-family: 'Barlow';
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    color: #D0D6F9;
}

.home-explore{
    width: 274px;
    height: 274px;
    background: #fff;
    font-family: 'Bellefair';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    letter-spacing: 2px;
    border-radius: 100%;
    color: #0B0D17;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 770px) {
    .home{
        background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url('../../assets/home/background-home-tablet.jpg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 1024px;
        max-width: 770px;
      }

    .home-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0;
        margin-top: 105px;
    }

    .home-text h1{ 
        font-size: 130px;
        line-height: 100px;
        margin: 24px 0;
    }

    .home-text p{
        font-size: 14px;
        text-align: center;
        margin-bottom: 220px;
    }
    
    .home-text h4 {
        font-size: 20px;
        line-height: 24px;
        text-align: center;
        letter-spacing: 3.375px;
    }

    .home-explore{
        width: 242px;
        height: 242px;

    }
   
}

@media screen and (max-width: 380px) {
    .home{
        background: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4)), url('../../assets/home/background-home-mobile.jpg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 667px;
        max-width: 375px;
      }
      
    
    .home-container{
        padding-top: 20px;
        padding-bottom: 0px;
        margin: 0;
    }
    
    .home-text{
        width: 327px;
    }
    
    .home-text h4{
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        letter-spacing: 2.7px;
    }
    
    .home-text h1{
        font-size: 80px;
        line-height: 100px;
        text-align: center;
    }
    
    .home-text p{
        font-size: 15px;
        line-height: 25px;
        text-align: center;
        margin-bottom: 100px;
    }
    
    .home-explore{
        width: 150px;
        height: 150px;
        font-size: 20px;
        line-height: 23px;
        text-align: center;
        letter-spacing: 1.25px;
    }
}
